.many-checks{
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    margin-top: 5vh;
    margin-left: 5vw;
    margin-right: 5vw;
}
.modal-filter-window{
    width: 75vw;
    z-index: 20;
    position: absolute;
    top: 10vh;
    left: 12.5vw;
    background: white;
    border: 2px solid silver;
    cursor: default;
    text-align: left;
}
#filter{
    display: none;
}
#close-block{
    position: absolute;
    top: 1vh;
    right: 1vw;
    background: red;
    color: #fff;
    font-family: 'Helvetica', 'Arial', sans-serif;
    font-size: 2vw;
    font-weight: bold;
    text-align: center;
    width: 2.3vw;
    height: 2.3vw;
    border-radius: 5px;
    cursor: pointer;
}
.filter-buttons{
    width: 22vw;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: auto;
    /* border: 2px solid red; */
    margin-bottom: 5vh;
    margin-top: 5vh;
}
.button{
    border: 2px solid silver;
    margin: auto;
    width: 10vw;
    text-align: center;
}
.search:hover{
    border: 2px solid rgb(66, 230, 51);
    background: rgb(66, 230, 51);
} 

.cancel:hover{
    border: 2px solid rgb(243, 77, 65);
    background: rgb(243, 77, 65);
}