.pagination-main{
    width: 30vw;
    margin: auto;
    margin-top: 2vh;
    margin-bottom: 2vh;
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    text-align: center;
    align-content: center;
}
.pagination-value{
    background: rgb(241, 241, 241);
    width: 3vw;
    cursor: pointer;
}
.pagination-value:hover{
    background: rgb(126, 126, 126);
}
.clicked-page, .clicked-page:hover{
    background: rgb(54, 54, 54);
    color: white;
    cursor: default;
}