.pokemon-list *{
    margin: 0;
    padding: 0;
    /* border: 2px solid red; */
}
.pokemon-list{
    width: 70vw;
    margin: auto;
    margin-top: 2vh;
    display: grid;
    /* grid-template-columns: repeat(5, 1fr); */
    grid-template: minmax(100px, 1fr) / repeat(auto-fit, minmax(200px, 1fr));
}
.pokemon{
    margin: auto;
    align-content: center;
    text-align: center;
    display: grid;
    border: 2px solid white;
}
.pokemon:hover{
    border: 2px solid rgb(54, 54, 54);
    background: rgb(54, 54, 54);
    color: white;
}

.pokemon>img{
    /* height: 20vh; */
}
.modalWindow{
    width: 75vw;
    z-index: 20;
    position: absolute;
    top: 10vh;
    left: 12.5vw;
    background: white;
}
#close-block{
  position: absolute;
  top: 1vh;
  right: 1vw;
  background: rgb(54, 54, 54);
  color: #fff;
  font-family: 'Helvetica', 'Arial', sans-serif;
  font-size: 2em;
  font-weight: bold;
  text-align: center;
  width: 2.3vw;
  height: 2.3vw;
  border-radius: 5px;
  cursor: pointer;
}
.modal{
    display: none;
}

.grid-container {
    display: grid;
    border: 2px solid rgb(54, 54, 54); 
    align-items: center;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-areas: "nameOfPokemon nameOfPokemon nameOfPokemon" "photo stat type1" "photo speed type1" "photo specialStats type2" "photo defenseAttack type2" "photo hp type3";
  }
  
  .grid-container *{
    /* border: 2px solid red; */
    padding-left: 10%;
  }

  .nameOfPokemon { 
      padding: 0;
      grid-area: nameOfPokemon; 
      text-align: center;
      text-transform: uppercase;
      font-size: 24pt;
    }
  
  .photo { grid-area: photo; }
  .photo>img{ 
      width: 90%;
      height: 90%;
      image-resolution: cover;
      margin: 5%;
      padding-left: 0;
   }
  
.stat { 
    grid-area: stat;
    font-size: 18pt;
    border-bottom: 3px solid rgb(54, 54, 54);
}
  
  .speed { 
    grid-area: speed;
    border-bottom: 3px solid rgb(54, 54, 54);
 }
  
  .specialStats { 
    grid-area: specialStats; 
    border-bottom: 3px solid rgb(54, 54, 54);
}
  
  .hp { 
    grid-area: hp;
    padding-bottom: 20px; 
}
  
  .type1 { 
    margin-left: 30%;
    grid-area: type1;
    width: 5vw;
    height: 5vw;
}
  
  .type2 { 
    grid-area: type2; 
    margin-left: 30%;
    width: 5vw;
    height: 5vw;
}

  .type3 { 
    grid-area: type3; 
    margin-left: 30%;
    width: 5vw;
    height: 5vw;
}
  
  .defenseAttack { 
    grid-area: defenseAttack; 
    border-bottom: 3px solid rgb(54, 54, 54);
}
