*{
    user-select: none;
}
.page-elements{
    display: grid;
    grid-template-columns: 1fr;
    /* border: 2px solid silver; */
    align-content: center;
    text-align: center;
}
.size-box{
    width: 5vw;
    margin: auto;
    outline: none;
}
.page-name{
    margin-top: 2vh;
    margin-bottom: 1vh;
    font-size: 32pt;
}
.sort, .filter, .refresh{
    /* z-index: 5;
    position: absolute;
    top: 12vh;
    left: 25vw; */
    width: 100%;
    margin: auto;
    margin-top: 1vh;
    border: 2px solid rgb(54, 54, 54);
    padding: 2px;
}
.buttons{
    display: grid;
    width: 30%;
    grid-template: minmax(1fr,) / repeat(auto-fit, minmax(250px, 1fr))    ;
    margin: auto;
    border: none;
    cursor: pointer;
}
.sort:hover, .filter:hover, .refresh:hover{
    background: rgb(54, 54, 54);
    color: white;
}